body {
  $font-default: "Montserrat";
  $font-small: 9px;
  $font-min: 12px;
  $font-reg: 14px;
  $font-med: 21px;
  background-color: #f2f2f2;
  display: grid;
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $font-default;
  font-size: $font-reg;

  .font-med {
    font-size: $font-med;
  }

  header.firstheader {
    height: 80vh;
    width: 100%;
    background-image: url("/src/assets/img/hikemain.webp");
    background-size: cover;
    background-position: bottom right;

    .maincontainer {
      width: 60%;
      height: 100%;
      margin: 0 auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 72px 1fr 2fr;
      grid-column-gap: 0px;
      grid-row-gap: 0px;

      #weatherwidget {
        grid-area: 1 / 1 / 2 / 3;
        display: flex;
        align-items: center;
        margin-top: 24px;
        padding-left: 20px;
        height: 48px;
        width: 100%;
        background-color: rgb(255, 255, 255, 0.8);
        border-radius: 3px;
        overflow: hidden;
        position: relative;
        font-family: $font-default;
        font-size: $font-min;

        .find-location {
          height: 30px;
          width: 30px;
          margin: 0 10px;
          background-color: #333333;
          border: 0;
          border-radius: 30px;
          cursor: pointer;
          transition: 0.2s ease-in-out;

          &:hover {
            background-color: #666666;
          }

          .iconstyle {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #fff;
          }
        }

        .city {
          font-weight: 600;
        }

        #temp,
        #temp2 {
          display: flex;
          align-items: center;
          margin-right: 6px;
        }

        #weather {
          position: absolute;
          z-index: 2;
          height: inherit;
          display: flex;
          align-items: center;
          left: 190px;

          .addred {
            color: #d01630;
          }

          .addorange {
            color: #d77e0c;
          }

          .addblue {
            color: #1193e1;
          }
        }

        #weatherwarsaw {
          display: flex;
          position: absolute;
          left: 190px;
          transition: 1s;
          align-items: center;
          z-index: 1;
          height: inherit;

          .addred {
            color: #d01630;
          }

          .addorange {
            color: #d77e0c;
          }

          .addblue {
            color: #1193e1;
          }
        }

        .iconcondition {
          height: 36px;
          width: 36px;
          margin: 0 6px;

          img {
            height: inherit;
            width: inherit;
          }
        }

        .rightbox {
          width: 48px;
          height: 48px;
          background-color: #333333;
        }
      }
    }

    .div2 {
      grid-area: 2 / 1 / 3 / 2;
    }
    .div3 {
      grid-area: 2 / 2 / 3 / 3;
    }
    .div4 {
      grid-area: 3 / 1 / 4 / 2;

      h1 {
        font-family: "Beth Ellen", cursive;
        font-size: 4.2vw;
        text-transform: lowercase;
        color: #ffffff;
        text-align: center;
        margin: 0;

        span {
          font-family: $font-default;
          margin-top: -3.5vw;
          font-size: 11.6vw;
          text-transform: uppercase;
          font-weight: 900;
          display: block;
        }
      }

      h2 {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 0.8vw;
        color: #ffffff;
        width: 70%;
        text-align: center;
        margin: 0 auto;
      }
    }

    .div5 {
      grid-area: 3 / 2 / 4 / 3;
    }
  }

  header.secheader {
    display: flex;
    width: 100%;

    .logo {
      margin: 10px 0;
      display: flex;

      h1 {
        font-family: "Beth Ellen", cursive;
        font-size: 36px;
        text-transform: lowercase;
        color: #aaaaaa;
        text-align: center;
        margin: 0;

        span {
          font-family: $font-default;
          margin-top: -10px;
          font-size: 36px;
          text-transform: uppercase;
          font-weight: 900;
          color: #333333;
        }
      }
    }
  }

  main {
    width: 100%;
  }

  main.mainarea {
    height: 15vh;
    display: grid;
    align-items: center;
  }

  main.secarea {
    display: grid;
    align-items: center;
    margin: 72px 0;
  
  .selected {
    display: flex;
    justify-content: space-between;
  }

  .selected-cont {
    padding: 35px 25px;
  }

  #country, #terrainType, #region {
    font-size: $font-med;
    font-weight: 700;
    color: #333;
  }

  .mountainpic {
    background-image: url('/src/assets/img/alps.png');
    background-repeat: no-repeat;
    height: 120px;
    width: 120px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    background-size: cover;
  }
  
  }

  .container {
    width: 60%;
    margin: 0 auto;
  }

  .choice {
    box-sizing: border-box;
    border-radius: 3px;
    background-color: #ffffff;
  
    h2 {
      color: #666666;
      margin: 0 0 6px 0;
      font-weight: 400;
      font-size: $font-reg;
    }

  }

  #formFirstStep {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #333333;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
  }

  #formSecondStep {
    display: flex;
    flex-direction: column;
    color: #333333;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;

    .formFirstLine,
    .formSecondLine {
      justify-content: space-between;
      display: flex;
      width: 100%;
      padding: 32px 25px;
      box-sizing: border-box;
      border: 1px solid #9c902552;
      border-radius: 3px;
    }

    .subtitle {
      text-align: left;
      text-transform: none;
      margin-bottom: -30px;
      background-color: #f2f2f2;
      z-index: 1;
      margin-top: 48px;
      width: -moz-max-content;
      width: max-content;
      margin-left: 1px;
      
      h2 {
        padding: 0 25px 0 24px;
        font-size: $font-med;
        color: #9c9025;
        font-weight: 300;
      }
    
    }
  }

  label {
    margin-right: 10px;
  }

  select,
  input,
  button {
    font-family: $font-default;
    font-size: $font-reg;
    font-weight: 400;
    cursor: pointer;
    color: #333333;
  }

  select {
    padding: 14px 36px 14px 14px;
    border-radius: 6px;
    border: 2px solid #eeeeee;
    background-image: url("data:image/svg+xml;utf8,<svg fill=%27black%27 height=%2724%27 viewBox=%270 0 24 24%27 width=%2724%27 xmlns=%27http://www.w3.org/2000/svg%27><path d=%27M7 10l5 5 5-5z%27/><path d=%27M0 0h24v24H0z%27 fill=%27none%27/></svg>");
    background-repeat: no-repeat;
    background-position-x: 95%;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-position-y: 10px;
    background-color: #ffffff;
  }

  input {
    padding: 14px;
    border-radius: 6px;
    border: 2px solid #eeeeee;
    background-color: #ffffff;
  }

  input[type="text"]:focus,
  select:focus {
    box-shadow: 0 1px 3px #dddddd;
  }

  input#numberOfPersons {
    max-width: 60px;
  }

  button.go {
    background-color: #c80a2c;
    border-radius: 6px;
    border: 2px solid #c80a2c;
    overflow: hidden;
    transition: 0.3s ease-in-out;
    width: -moz-fit-content;
    width: fit-content;

    &:hover > span {
      position: relative;
      transition: 0.3s ease-in-out;
      transform: translate(100%);
    }

    span {
      color: #ffffff;
      display: block;
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      padding: 14px 28px;
      transition: 0.3s ease-in-out;
      position: relative;

      &:before {
        content: "go!";
        position: absolute;
        left: -62%;
      }
    }
  }

  footer {
    background-color: #ffffff;
    height: 5vh;
    min-height: 48px;
    width: 100%;
    display: flex;
    align-items: center;

    p {
      font-size: $font-small;
      color: #999999;
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: 1px;
      text-align: center;

      span {
        color: #333333;
        padding: 0 10px;
      }
    }
  }

  .mtn {
    width: 100%;
    background-image: url("/src/assets/img/mtnfooter.png");
    background-position: top center;
    height: 300px;
  }
}